@import url(https://fonts.googleapis.com/css2?family=Lastica&family=Lobster&display=swap);
/* Import custom fonts */

/* General Page Layout */
.landing-wrapper {
  font-family: 'Segoe UI', sans-serif;
  color: white;
  overflow-x: hidden;
}

/* === Hero Section === */
.hero-section {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.bg-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 8%;
  z-index: 2;
  animation: fadeIn 2s ease-in;
}

.overlay-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  text-align: left;
}

.hero-quote {
  font-family: 'Lobster', cursive;
  font-size: 2rem;
  color: #b3e5fc;
  margin-bottom: 1rem;
}

.hero-logo-title {
  font-family: 'Lastica', sans-serif;
  font-size: 4rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  margin: 0;
  color: white;
}

.hero-subtext {
  font-style: italic;
  font-size: 1.25rem;
  margin-top: 0.5rem;
}

.hero-logo {
  width: 500px;
  height: auto;
  margin-right: auto;
  transform: translateX(100px); /* push slightly left */
}

/* === About Section === */
.about-section {
  display: flex;
  flex-wrap: wrap;
  background: #241532;
  color: #ffffff;
  padding: 60px 20px;
  align-items: center;
  justify-content: center;
}

.about-text {
  flex: 1 1;
  min-width: 300px;
  padding: 20px;
}

.about-gif {
  flex: 1 1;
  min-width: 500px;
  padding: 20px;
  text-align: center;
}

.about-gif img {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
}

/* === Team Section === */
.team-section {
  background: #1f1f1f;
  padding: 60px 20px;
  text-align: center;
}

.team-section h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.team-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  grid-gap: 40px;
  gap: 40px;
}

.team-member {
  background: #2a2a2a;
  padding: 20px;
  border-radius: 12px;
  width: 180px;
  color: #fff;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0,0,0,0.3);
}

.member-pic {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 10px;
}

.social-links a {
  margin: 0 5px;
  font-size: 1.2rem;
  color: #ccc;
  text-decoration: none;
  transition: color 0.2s;
}

.social-links a:hover {
  color: #00ccff;
}

/* Fade animation for overlay */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes turtleMove {
  0% {
    left: 0;
  }
  100% {
    left: calc(100% - 60px);
  }
}

@keyframes progressFill {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}

.recommender-bg {
    background: linear-gradient(135deg, #2d114d, #2b145f);
    background-size: cover;
    min-height: 100vh;
    width: 100%;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 1rem;
    color: white;
  }
  
  .question-slide {
    display: flex;
    width: 100%;
    min-height: 80vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  .question-card {
    background-color: #3d265c;
    border-radius: 1rem;
    padding: 2rem;
    width: 100%;
    max-width: 500px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
    text-align: center;
  }
  
  .question-card h2 {
    color: white;
    margin-bottom: 1rem;
    font-size: 1.25rem;
  }
  
  .question-card select {
    width: 100%;
    padding: 0.75rem;
    background-color: #d1c3ff;
    color: black;
    border: none;
    border-radius: 0.5rem;
    font-size: 1rem;
    cursor: pointer;
  }
  
  .question-card option {
    background-color: #e8d7ff;
    color: black;
  }
  
  /* Back arrow button */
  .back-button {
    margin-top: 1rem;
    font-size: 1rem;
    color: white;
    cursor: pointer;
    transition: opacity 0.3s ease;
  }
  
  .back-button:hover {
    opacity: 0.8;
    text-decoration: underline;
  }
  
  /* Button group styling */
  .button-group {
    display: flex;
    background-color: #8c5eff;
    color: white;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 2rem;
    flex-wrap: wrap;
  }
  
  /* Loading animation */
  @keyframes progressFill {
    0% { width: 0%; }
    100% { width: 100%; }
  }
  
  @keyframes turtleMove {
    0% { left: 0%; }
    100% { left: 90%; }
  }
  
  .animate-progressFill {
    animation: progressFill 2s linear forwards;
  }
  
  .animate-turtleMove {
    animation: turtleMove 2s linear forwards;
  }
  .group:hover .group-hover\:block {
    display: block;
  }  

  .tooltip-container {
    position: relative;
    display: inline-block;
  }
  
  .tooltip-icon {
    background-color: #6b7280;
    color: white;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-left: 8px;
    z-index: 1;
  }
  
  .tooltip-text {
    visibility: hidden;
    opacity: 0;
    width: 280px;
    background-color: #333;
    color: #fff;
    text-align: left;
    border-radius: 6px;
    padding: 10px;
    position: absolute;
    top: 50px; /* Adjust as needed */
    right: 0;
    z-index: 2;
    transition: opacity 0.3s;
    white-space: pre-line;
  }
  
  .tooltip-container:hover .tooltip-text {
    visibility: visible;
    opacity: 1;
  }
.results-bg {
    background: linear-gradient(135deg, #2d114d, #2b145f);
    min-height: 100vh;
    padding: 2rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
  }
  
  .button-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .custom-input {
    padding: 0.75rem;
    border-radius: 0.5rem;
    border: none;
    width: 100%;
    max-width: 320px;
    background-color: #d1c3ff;
    color: #1f1f1f;
    text-align: center;
    font-size: 1rem;
  }
  
  .custom-button {
    background-color: #8c5eff;
    color: white;
    padding: 0.6rem 1.5rem;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .custom-button:hover {
    background-color: #a07eff;
  }
  
  .results-card {
    background-color: white;
    color: black;
    border-radius: 1rem;
    padding: 2rem;
    width: 100%;
    max-width: 700px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
    margin-top: 1rem;
  }
  
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

